export async function raceWithSignal(promise, signal) {
    if (signal === undefined) {
        return promise;
    }
    return Promise.race([
        promise.catch((err) => {
            if (!signal?.aborted) {
                throw err;
            }
            else {
                return undefined;
            }
        }),
        new Promise((_, reject) => {
            signal.addEventListener("abort", () => {
                reject(new Error("Aborted"));
            });
            // Must be here inside the promise to avoid a race condition
            if (signal.aborted) {
                reject(new Error("Aborted"));
            }
        }),
    ]);
}
